<template>
  <img class="avatar-container" :src="url" :style="{ width: size + 'px', height: size + 'px' }" alt="" />
</template>

<script>
export default {
  props: {
    url: {
      type: String,
      require: true,
    },
    size: {
      type: Number,
      default:150
    },
  },
};
</script>

<style scoped>
.avatar-container{
    display: block;
    border-radius: 50%;
    object-fit: cover; /*防止图片比例失衡*/
  
}
</style>