<template>
  <div class="blog-toc-container">
    <h2>目录</h2>
    <RightList :list="tocWithSelect" @select="handleSelect"></RightList>
  </div>
</template>

<script>
import RightList from "./RightList";
import {debounce} from "@/utils"
export default {
  components: {
    RightList,
  },
  props: {
    toc: {
      type: Array,
    },
  },
  data() {
    return {
      activeAnchor: "",
    };
  },
  computed: {
    //根据toc属性以及activeAnchor得到带有isSelcet属性的数组
    tocWithSelect() {
      const getTOC = (toc = []) => {
        return toc.map((t) => ({
          ...t,
          isSelect: t.anchor === this.activeAnchor,
          children: getTOC(t.children),
        }));
      };
      return getTOC(this.toc);
    },
    //根据toc得到它们对应的元素数组
    //[dom1-概述,dom2-简单请求]
    doms() {
      const doms = [];
      const addToDoms = (toc) => {
        for (const t of toc) {
          doms.push(document.getElementById(t.anchor));
          // console.log(t.anchor);
          if (t.children && t.children.length) {
            addToDoms(t.children);
          }
        }
      };
      addToDoms(this.toc);
      return doms;
    },
  },
  created() {
   this.setSelectDebounce = debounce(this.setSelect, 50);
    this.$bus.$on("mainScroll", this.setSelectDebounce)
  },
  destroyed(){
     this.$bus.$off("mainScroll", this.setSelectDebounce)
  },
  methods: {
    handleSelect(item) {
      location.hash = item.anchor;
    },

    //设置activeAnchor为正确的值
    setSelect() {
      this.activeAnchor = "";  //由于后续要重新设置，先清空之前的状态
      const range = 200;
      for (const dom of this.doms) {
        //检查该dom元素是否该被选中
        if (!dom) {
          continue;
        }
        //选中规则：若该元素标题离视口距离在200px以内、在上个元素末尾200以内、在自己元素200px以内，则默认选中
        //得到元素离视口顶部的距离
        const top = dom.getBoundingClientRect().top;
        if (top >= 0 && top <= range) {
          //在规定的范围内
          this.activeAnchor = dom.id;
          return
        } else if (top > range) {
          return
          //在规定的范围下方
        } else {
          //在规定的范围上方
          this.activeAnchor = dom.id;
        }
      }
    },
  },
};
</script>


<style lang="less" scoped>
.blog-toc-container {
  h2 {
    font-weight: bold;
    letter-spacing: 2px;
    font-size: 1em;
    margin: 0;
  }
}
</style>