<template>
  <div
    class="carousel-item-container"
    ref="container"
    @mousemove="handleMouseMove"
    @mouseleave="handleMouseLeave"
  >
    <div class="carousel-img" :style="imagePosition" ref="image">
      <ImageLoader
        @load="this.showWords"
        :src="carousel.bigImg"
        :placeholder="carousel.midImg"
      ></ImageLoader>
    </div>
    <!-- <div class="test">横坐标：{{ this.mouseX }}; 纵坐标：{{ this.mouseY }}</div> -->
    <div class="title" ref="title">{{ carousel.title }}</div>
    <div class="desc" ref="desc">{{ carousel.description }}</div>
  </div>
</template>

<script>
import ImageLoader from "@/components/ImageLoader";
export default {
  props: ["carousel"],
  components: {
    ImageLoader,
  },
  data() {
    return {
      titleWidth: 0,
      descWidth: 0,
      containerSize: null, //外层容器的尺寸
      innerSize: null, //内层放大图片的尺寸
      mouseX: 0, //鼠标横坐标
      mouseY: 0, //鼠标纵坐标
    };
  },
  mounted() {
    this.titleWidth = this.$refs.title.clientWidth;
    this.descWidth = this.$refs.desc.clientWidth;
    // console.log(this.titleWidth, this.descWidth);
    // this.showWords();
    this.setSize();
    this.mouseX=this.center.x;
    this.mouseY=this.center.y
    window.addEventListener("resize", this.setSize);
  },
  destroyed() {
    window.removeEventListener("resize", this.setSize);
  },
  computed: {
    //得到图片坐标
    imagePosition() {
      if (!this.innerSize || !this.containerSize) {
        return {};
      }
      const extraWidth = this.innerSize.width - this.containerSize.width; //多出的快读
      const extraHeight = this.innerSize.height - this.containerSize.height; //多出的高度
      //   console.log(extraWidth, extraHeight);
      const left = (-extraWidth / this.containerSize.width) * this.mouseX;
      const top = -extraHeight/ this.containerSize.height * this.mouseY
      return {
        // left: left + "px",
        // top: top + "px"  优化：使用left和top会引起reflow
        transform: `translate(${left}px,${top}px)`
      };
    },
    center(){
        return {
            x:this.containerSize.width / 2,
            y:this.containerSize.height / 2
        }
    }
  },
  methods: {
    //调用该方法 显示文字
    showWords() {
      this.$refs.title.style.opacity = 1;
      this.$refs.title.style.width = 0;
      //强制渲染一次，因为浏览器渲染是异步的，如果不再次强制渲染一次 width=0不会存在
      //强制渲染方法：读取任意一个dom元素的宽高等属性即可
      this.$refs.title.clientWidth; //reflow
      this.$refs.title.style.transition = "1s";
      this.$refs.title.style.width = this.titleWidth + "px";

      this.$refs.desc.style.opacity = 1;
      this.$refs.desc.style.width = 0;
      //强制渲染一次，因为浏览器渲染是异步的，如果不再次强制渲染一次 width=0不会存在
      //强制渲染方法：读取任意一个dom元素的宽高等属性即可
      this.$refs.desc.clientWidth; //reflow
      this.$refs.desc.style.transition = "2s 1s";
      this.$refs.desc.style.width = this.descWidth + "px";
    },
    setSize() {
      this.containerSize = {
        width: this.$refs.container.clientWidth,
        height: this.$refs.container.clientHeight,
      };
      this.innerSize = {
        width: this.$refs.image.clientWidth,
        height: this.$refs.image.clientHeight,
      };

    //   console.log(this.containerSize, this.innerSize);
    },
    handleMouseMove(e) {
      const rect = this.$refs.container.getBoundingClientRect();
      this.mouseX = e.clientX - rect.left;
      this.mouseY = e.clientY - rect.top;
      // console.log(this.mouseX,this.mouseY)
    },
    handleMouseLeave(){
        this.mouseX = this.center.x;
        this.mouseY = this.center.y 
    }
  },
};
</script>

<style lang="less" scoped>
@import "~@/styles/var.less";
.carousel-item-container {
  //   background: @dark;
  width: 100%;
  height: 100%;
  color: #fff;
  position: relative;
  overflow: hidden;
}
.carousel-img {
  width: 110%;
  height: 110%;
  position: absolute;
  left: 0;
  top: 0;
  transition: .3s;
}
.title,
.desc {
  position: absolute;
  left: 30px;
  color: #fff;
  text-shadow: 1px 0 0 rgba(0, 0, 0, 0.5), 1px 0 0 rgba(0, 0, 0, 0.5),
    0px 1 0 rgba(0, 0, 0, 0.5), 0px -1 0 rgba(0, 0, 0, 0.5);
  white-space: nowrap;
  overflow: hidden;
  opacity: 0;
}
.title {
  top: calc(50% - 40px);
  font-size: 2em;
}
.desc {
  top: calc(50% + 20px);
  font-size: 1.2em;
  color: lighten(@gray, 20%);
}
</style>
