<template>
  <div class="Image-loader-container">
    <img alt="" class="placeholder" :src="placeholder" v-if="!everythingDone"/>
    <img
      :src="src"
      alt=""
      class="origin"
      @load="handleLoad"
      :style="{ opacity: originOpacity, transition:`${duration}ms` }"
    />
  </div>
</template>

<script>
export default {
  props: {
    src: {
      type: String,
      require: true,
    },
    placeholder: {
      type: String,
      require: true,
    },
    duration: {
      type: Number,
      require: false,
      default: 500,
    },
  },
  data() {
    return {
      originLoaded: false, //原图是否加载完成
      everythingDone:false
    };
  },
  computed: {
    originOpacity() {
      return this.originLoaded ? 1 : 0;
    },
  },
  methods: {
    handleLoad() {
      this.originLoaded = true;
      console.log("原图加载完成");
      setTimeout(() => {
        this.everythingDone = true;
        this.$emit("load")
      }, this.duration);
    },
  },
};
</script>

<style lang="less" scoped>
@import "~@/styles/mixin.less";
.Image-loader-container {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  img {
    .self-fill();
    object-fit: cover;
  }
}
.placeholder {
  filter: blur(2vw);
}
</style>