<template>
  <ul class="right-list-container">
    <li v-for="(item, i) in list" :key="i">
      <span :class="{ active: item.isSelect }" @click="handleClick(item)">
        {{ item.name }}</span
      >
      <span v-if="item.aside" class="aside" :class="{ active: item.isSelect }" @click="handleClick(item)">
        {{item.aside}}
      </span>
      <RightList :list="item.children" @select="handleClick"></RightList>
    </li>
  </ul>
</template>

<script>
export default {
  name: "RightList",
  props: {
    //[{name:"xxx",isSelect:true,children:[{name:"yyy",isSelect:false}]}]
    list: {
      type: Array,
      default: () => [], //默认值为数组或对象时 需用函数返回值的形式设置
    },
  },
  methods: {
    handleClick(item) {
      // console.log(item)
      if (!item.isSelect) {
        this.$emit("select", item);
      }
    },
  },
};
</script>

<style lang="less" scoped>
@import "~@/styles/var.less";
.right-list-container {
  list-style: none;
  padding: 0;
  .right-list-container {
    margin-left: 1em;
  }
  li {
    min-height: 40px;
    line-height: 40px;
    font-size: 14px;
    cursor: pointer;
    .active {
      font-weight: bold;
      color: @warn;
    }
  }
  .aside{
    font-size: 14px;
    margin-left: 1em;
    color:@gray
  }
}
</style>